.smooth-dnd-container *{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.smooth-dnd-disable-touch-action{
  -ms-touch-action: none;
      touch-action: none;
}

.smooth-dnd-container{
  position: relative;
}

.smooth-dnd-container.vertical{
}

.smooth-dnd-container.horizontal{
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.smooth-dnd-container.horizontal .smooth-dnd-draggable-wrapper{
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  float: left;
  height: 100%;
  /* display: inline-block; */
}


.smooth-dnd-draggable-wrapper {
  overflow: hidden;
}

.smooth-dnd-draggable-wrapper.animated{
  -webkit-transition: -webkit-transform ease;
  transition: -webkit-transform ease;
  -o-transition: transform ease;
  transition: transform ease;
  transition: transform ease, -webkit-transform ease;
}

.smooth-dnd-ghost {   
  
}

.smooth-dnd-ghost *{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.smooth-dnd-ghost.animated{
  -webkit-transition: all ease-out;
  -o-transition: all ease-out;
  transition: all ease-out;
}

.clearfix:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
}

.smooth-dnd-no-user-select{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.smooth-dnd-stretcher-element{
  height: 2px;
  background-color: transparent;
  /* transition: transform 0.18s ease; */
}


